import { useState, useEffect } from 'react';
import Brand from "./Brand";
import UserWrapper from "./UserWrapper";
import { Link } from 'react-router-dom';
import { IoMdLogIn as LoginIcon } from "react-icons/io";
import useUser from '../../hooks/use-user';
const Topbar = props => {
    const isLoggedIn = !!localStorage.getItem('uid');
    const [isRegister, setIsRegister] = useState(false);
    const user = useUser(localStorage.getItem('uid'))

    useEffect(() => {
        (window.location.pathname === '/register') && setIsRegister(true);
    }, [])
    return (
        <section className={(isRegister) ? 'invisible topbar' : 'topbar'}>
            <Brand />
            {isLoggedIn
             ? <UserWrapper user={user.displayName}/>
             : <Link className="user-wrapper" to="/register">Registro<LoginIcon /></Link>
            }
           
        </section>
    )
}

export default Topbar;